body {
  background: #faf5f0;
}

.container {
  padding: 0 20px;
  margin: 0 auto;
  max-width: 1360px;
}

.nav {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 5;
}
.nav::before {
  content: "";
  display: block;
  width: 100%;
  height: 100%;
  position: absolute;
  z-index: -1;
  backdrop-filter: blur(20px);
  background: rgba(50, 48, 45, 0);
}
.nav__row {
  position: relative;
  display: grid;
  grid-template-columns: 1fr auto 1fr;
  gap: 20px;
  align-items: center;
  padding: 18px 0;
}
.nav__rate {
  border: 1px solid rgba(255, 255, 255, 0.06);
  border-radius: 34px;
  padding: 4px 20px 4px 4px;
  background: rgba(240, 231, 223, 0.12);
  display: grid;
  grid-template-columns: auto 1fr;
  gap: 8px;
  align-items: center;
}
.nav__rate span {
  width: 40px;
  height: 40px;
  border-radius: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  box-shadow: 0 2px 2px 0 rgba(58, 58, 58, 0.08);
  background: #fff;
}
.nav__rate p {
  font-family: "KyivType Sans", sans-serif;
  font-weight: 500;
  font-size: 14px;
  line-height: 1.24;
  letter-spacing: -0.01em;
  color: #fff;
}
.nav__rate p + p {
  margin: 2px 0 0;
  font-family: "KyivType Sans", sans-serif;
  font-weight: 100;
  font-size: 12px;
  line-height: 1.24;
  letter-spacing: -0.01em;
  color: rgba(255, 255, 255, 0.7);
}
.nav__rate_text {
  font-family: "KyivType Sans", sans-serif;
  font-weight: 100;
  font-size: 12px;
  line-height: 1.24;
  letter-spacing: -0.01em;
  color: #fff;
  max-width: 210px;
  margin: 0 0 0 12px;
  opacity: 0.7;
}
.nav__rate_wrapper {
  display: flex;
  justify-content: start;
  align-items: center;
}
.nav__soc {
  margin: 0 12px 0 auto;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 8px;
}
.nav__soc a {
  display: flex;
  justify-content: center;
  align-items: center;
  border: 1px solid rgba(255, 255, 255, 0.06);
  border-radius: 90px;
  backdrop-filter: blur(20px);
  box-shadow: 0 2px 2px 0 rgba(58, 58, 58, 0.08);
  background: rgba(255, 255, 255, 0.12);
  width: 40px;
  height: 40px;
}
.nav__phone {
  margin: 0 24px 0 0;
}
.nav__phone a {
  font-family: "KyivType Sans", sans-serif;
  font-weight: 500;
  font-size: 14px;
  line-height: 1.24;
  letter-spacing: -0.01em;
  color: #fff;
}
.nav__phone p {
  margin: 4px 0 0;
  font-family: "KyivType Sans", sans-serif;
  font-weight: 0;
  font-size: 12px;
  line-height: 1.24;
  letter-spacing: -0.01em;
  color: #fff;
  opacity: 0.7;
}
.nav__list {
  padding: 20px 0;
  border-bottom: 1px solid rgba(255, 255, 255, 0.12);
  border-top: 1px solid rgba(255, 255, 255, 0.12);
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.nav__list a {
  font-family: "KyivType Sans", sans-serif;
  font-weight: 500;
  font-size: 13px;
  line-height: 1.24;
  letter-spacing: 0.06em;
  text-transform: uppercase;
  color: #fff;
}

.btn__gold {
  border-radius: 999px;
  padding: 16px 24px;
  box-shadow: 1px 3px 7px 0 rgba(113, 87, 71, 0.2), 5px 13px 13px 0 rgba(113, 87, 71, 0.17), 11px 28px 18px 0 rgba(113, 87, 71, 0.1), 19px 51px 22px 0 rgba(113, 87, 71, 0.03), 29px 79px 24px 0 rgba(113, 87, 71, 0);
  background: linear-gradient(180deg, #d4b26f 0%, #a87c25 100%);
  display: flex;
  justify-content: center;
  align-items: center;
  width: max-content;
  border: none;
  gap: 8px;
  font-family: "KyivType Sans", sans-serif;
  font-weight: 500;
  font-size: 14px;
  line-height: 1.24;
  letter-spacing: -0.01em;
  text-transform: uppercase;
  color: #fff;
}
.btn__gold span {
  width: 24px;
  height: 24px;
  transition: all 0.3s;
  display: flex;
  justify-content: center;
  align-items: center;
  background: #fff;
  border-radius: 100%;
}
.btn__gold:hover span {
  transform: rotate(45deg);
}
.btn__wht {
  display: flex;
  justify-content: center;
  align-items: center;
  width: max-content;
  border: none;
  gap: 8px;
  border-radius: 999px;
  padding: 12px 8px 12px 12px;
  background: #fff;
  font-family: "Golos Text", sans-serif;
  font-weight: 500;
  font-size: 14px;
  line-height: 1;
  text-transform: uppercase;
  color: #3a3a3a;
}
.btn__wht span {
  width: 24px;
  height: 24px;
  transition: all 0.3s;
  display: flex;
  justify-content: center;
  align-items: center;
  background: #cda658;
  border-radius: 100%;
}
.btn__wht:hover span {
  transform: rotate(45deg);
}

.header {
  position: relative;
  z-index: 2;
  overflow: hidden;
  border-radius: 0 0 16px 16px;
}
.header__bg {
  position: absolute;
  z-index: -1;
  pointer-events: none;
  width: 100%;
  height: 100%;
  object-fit: cover;
  top: 0;
  left: 0;
}
.header .swiper {
  overflow: visibile;
}
.header__slide {
  min-height: 100vh;
  padding: 204px 0 286px;
}
.header__slide .title {
  font-family: "GT Eesti Pro Text", sans-serif;
  font-weight: 375;
  font-size: 60px;
  line-height: 0.94;
  letter-spacing: -0.04em;
  color: #fff;
  max-width: 863px;
}
.header__slide .title + p {
  margin: 24px 0 0;
  max-width: 456px;
  font-family: "KyivType Sans", sans-serif;
  font-weight: 400;
  font-size: 18px;
  line-height: 1.4;
  letter-spacing: -0.01em;
  color: #fff;
}
.header__slide .btn__gold {
  margin: 32px 0 0;
}
.header .swiper-pagination {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 6px;
  transform: none;
  margin: 0;
  inset: auto;
  width: 100%;
  left: 50%;
  transform: translateX(-50%);
  bottom: 40px;
}
.header .swiper-pagination span {
  width: 142px;
  transition: all 0.3s;
  backdrop-filter: blur(4px);
  background: rgba(255, 255, 255, 0.12);
  margin: 0 !important;
  display: block;
  height: 4px;
  border-radius: 32px;
  opacity: 1;
}
.header .swiper-pagination span.swiper-pagination-bullet-active {
  width: 260px;
  background: #fff;
}
.header__pag {
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
  bottom: 60px;
  display: flex;
  justify-content: center;
  align-items: stretch;
  gap: 6px;
  transform: none;
  margin: 0;
  width: 100%;
  left: 50%;
  transform: translateX(-50%);
  z-index: 3;
}
.header__pag_item {
  width: 142px;
  transition: all 0.3s;
  display: flex;
  border: 1px solid rgba(255, 255, 255, 0.06);
  border-radius: 20px;
  backdrop-filter: blur(16px);
  background: rgba(255, 255, 255, 0.12);
  padding: 4px;
}
.header__pag_item div {
  padding: 12px;
}
.header__pag_item img {
  width: 0;
  transition: all 0.3s;
  height: 1px;
  min-height: 100%;
  border-radius: 17px;
  object-fit: cover;
}
.header__pag_item p {
  transition: all 0.3s;
}
.header__pag_item p:nth-child(1) {
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 90px;
  width: 32px;
  height: 32px;
  font-family: "Golos Text", sans-serif;
  font-weight: 500;
  font-size: 15px;
  line-height: 1;
  text-transform: uppercase;
  color: #1b1b1b;
  border: 1px solid rgba(255, 255, 255, 0.12);
  background: rgba(255, 255, 255, 0.12);
}
.header__pag_item p:nth-child(2) {
  margin: 14px 0 0;
  font-family: "KyivType Sans", sans-serif;
  font-weight: 400;
  font-size: 12px;
  line-height: 1.24;
  letter-spacing: -0.01em;
  color: #fff;
}
.header__pag_item.active {
  width: 260px;
  background: #fff;
}
.header__pag_item.active p:nth-child(1) {
  border: 1px solid rgba(213, 175, 98, 0.6);
  color: #1b1b1b;
}
.header__pag_item.active p:nth-child(2) {
  color: #1b1b1b;
}
.header__pag_item.active img {
  width: 124px;
}